"use client";
import { useSpaceData } from "@/src/context/SpaceDataContext";
import { Technology } from "@/src/types";
import React, { useEffect, useState } from "react";

export default function page() {
  const [technology, setTechnology] = useState<Technology>();
  const { data } = useSpaceData();

  useEffect(() => {
    if (!data) return;

    setTechnology(
      data.technology.find(
        (technology: Technology) => technology.name === "Launch Vehicle"
      )
    );
  }, [data]);

  if (!data) return null;
  return (
    <div className="h-full flex flex-col lg:flex-row max-w-[1500px] gap-5 mx-auto border">
      <section className="flex-1 bg-blue-500 flex gap-4 items-center flex-col">
        <p className=" text-2xl mx-auto barlowCondensed text-silver font-bold lg:mx-0 lg:hidden">
          <span className="text-zinc-600">03</span>SPACE LAUNCH 101
        </p>
        <div className="relative flex-1 bg-red-500 w-full"></div>
      </section>
      <section className="flex-1 bg-blue-500"></section>
    </div>
  );
}
